import React , { Component } from 'react';
import './current_game.css';

let interval;
class Current_game extends Component {
   constructor(props){
       super(props);
       this.state = {
            game_time: 100
       };
       
   }
   formart_time(){
      let hours = Math.floor(this.state.game_time / 60 / 60);
      let minues = Math.floor(this.state.game_time / 60 % 60);
      let seconds = parseInt(this.state.game_time % 60);
      hours = hours >= 10 ? hours : '0' + hours
      minues = minues >= 10 ? minues : '0' + minues
      seconds = seconds >= 10 ? seconds : '0' + seconds
      return `<span>${hours}:</span><span>${minues}:</span><span>${seconds}</span>`
   }
   componentDidMount(){
       console.log(this.state.game_time)
        interval = setInterval(() => {
        //    console.log(this.state.game_time)
           if(this.state.game_time > 0){
               this.state.game_time --
               this.setState({
                    game_time:this.state.game_time
               })
           }else{
               clearInterval(interval)
                this.state.game_time  = 0
                this.setState({
                    game_time:this.state.game_time
               })
           }
       }, 1000);
   }
   componentWillUnmount(){
       clearInterval(interval)
   }
   render(){
       return (
           <div className="current_game_page">
                <div className="group_content_2_timer_content">
                    <p className="app-color-blue  flex-box flex-ali-center flex-jus-center">开奖倒计时
                        <img src={require('../../image/question_blue.png')} alt="question_blue" className="group_content_2_timer_question_blue"/>
                    </p>
                    <p className="group_content_2_timer_time flex-box flex-ali-center flex-jus-center" dangerouslySetInnerHTML ={{__html:this.formart_time()}}></p>
                    {/* <p className="group_content_2_timer_time flex-box flex-ali-center flex-jus-center"><span>22:</span><span>22:</span><span>23</span></p> */}
                    <div className="group_content_2_progress_bar">
                        <img src={require('../../image/prigress_point.png')} alt="prigress_point" className="prigress_point"/>
                    </div>
                </div>
                <ul className="pool_detail">
                    <li className="pool_list_ele">
                        <div className="flex-box flex-ali-center flex-jus-bt">
                            <span className="pool_label">奖池金额</span>
                            <div className="flex-box flex-ali-center">
                                <span className="pool_text_color">289738293</span>
                                <img src={require('../../image/eth.png')} alt="eth" className="pool_eth_icon"/>
                            </div>
                        </div>
                        <p>≈8273638CNY</p>
                    </li>
                    <li className="pool_list_ele">
                        <div className="flex-box flex-ali-center flex-jus-bt">
                            <span className="pool_label">我的钥匙</span>
                            <div className="flex-box flex-ali-center">
                                <span className="pool_text_color">19</span>
                                <img src={require('../../image/key_icon_pink.png')} alt="eth" className="pool_eth_icon"/>
                            </div>
                        </div>
                        <p>玩家共购买38292枚钥匙</p>
                    </li>
                    <li className="pool_list_ele">
                        <div className="flex-box flex-ali-center flex-jus-bt">
                            <span className="pool_label">我的分红</span>
                            <div className="flex-box flex-ali-center">
                                <span className="pool_text_color">289738293</span>
                                <img src={require('../../image/eth.png')} alt="eth" className="pool_eth_icon"/>
                            </div>
                        </div>
                        <p>≈8273638CNY</p>
                    </li>
                </ul>
           </div>
       )
  }
}

export default Current_game;


