import React , { Component } from 'react';
import './securit_center.css';

import Header from '../../components/header/header';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class Securit_center extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
            <div className="securit_center_page">
                <Header>
                    <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                    </div>
                    <div className='header_title'>安全中心</div>
                    <div className='header_right'></div>
                </Header>
                <div id='app___page_content' className="securit_center_content">
                    <div className="securit_center_main">
                        <div className="contain-list flex-box flex-ali-center flex-jus-bt">
                            <span>更改手机</span>
                            <img className="imgright fr" src={require('../../image/mine/right.png')} alt="imgright"/>
                        </div>
                        <div className="contain-list flex-box flex-ali-center flex-jus-bt">
                            <span>修改密码</span>
                            <img className="imgright fr" src={require('../../image/mine/right.png')} alt="imgright"/>
                        </div>
                    </div>
                </div>
            </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Securit_center);
// export default Securit_center;


