import React , { Component } from 'react';
import './mine.css';

import Footer from '../../components/footer/footer';

import {clear_storage, deleted_storage} from '../../js/storage'

import { Modal, Button, message, Spin, Alert  } from 'antd';
// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'
message.config({
    top: '45%'
});


const confirm = Modal.confirm;

class Mine extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.showConfirm = this.showConfirm.bind(this)
   }
    next_page(url){
        this.props.history.push(url)
    }
    componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
    }
    showConfirm() {
        let _this = this;
        confirm({
            title: '退出提醒',
            content: '您确认要退出Fomo3dd吗？',
            okText:"确认",
            cancelText:"取消",
            onOk() {
                // clear_storage();
                deleted_storage('fomo3dd')
                _this.props.history.push('/login')
                // return 
                // new Promise((resolve, reject) => {
                // setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
                // }).catch(() => console.log('Oops errors!'));
            },
            onCancel() {},
        });
    }
   render(){
       return (
           <div>
                <div id='app___page_content' className="mine_page">
                    <div className="user_msg_content flex-box flex-ali-center flex-jus-bt flex-col">
                        <div className="user_msg_center flex-box flex-ali-center flex-jus-bt">
                            <div className="user_icon">
                                <img src={require('../../image/transition/default_icon.png')} alt="default_icon" className="default_icon"/>
                            </div>
                            <div className="user_msg flex-box flex-ali-center flex-jus-bt">
                                <div className="user_name_address_content">
                                    <p className="user_name">mmiaolianh202</p>
                                    <p className="text-color-gray">钱包地址</p>
                                    <p className="text-color-gray address_content">http://www.baidu.comhttp://www.baidu.com</p>
                                </div>
                                <div className="flex-box flex-ali-center flex-jus-center flex-col erweima_content">
                                    <img src={require('../../image/mine/erweima.png')} alt="erweima" className="erweima"/>
                                    <p className="invited_label">邀请</p>
                                </div>
                            </div>
                        </div>
                        <div className="user_num_bar flex-box flex-ali-center flex-jus-bt">
                            <div className="user_num_ele">
                                <p className="user_num_ele_num">245</p>
                                <p className="user_num_ele_label">欲望之岛</p>
                            </div>
                            <div className="user_num_ele">
                                <p className="user_num_ele_num">278</p>
                                <p className="user_num_ele_label">冒险之岛</p>
                            </div>
                            <div className="user_num_ele">
                                <p className="user_num_ele_num">189</p>
                                <p className="user_num_ele_label">令牌</p>
                            </div>
                            <div className="user_num_ele">
                                <p className="user_num_ele_num">4980</p>
                                <p className="user_num_ele_label">ETH账户</p>
                            </div>
                        </div>
                    </div>

                    <div className="tool_bar_content">
                        <div className="tool_bar_title">常用工具</div>
                        <ul className="flex-box flex-ali-center flex-wrap tool_bar">
                            <li onClick={()=>this.next_page('/about_us')}>
                                <img src={require('../../image/mine/about_us.png')} alt="about_us" className="about_us list_icon" />
                                <p>关于我们</p>
                            </li>
                            <li onClick={()=>this.next_page('/securit_center')}>
                                <img src={require('../../image/mine/safe_center.png')} alt="about_us" className="about_us list_icon" />
                                <p>安全中心</p>
                            </li>
                            <li onClick={()=>this.next_page('/payment_type')}>
                                <img src={require('../../image/mine/payment_type.png')} alt="about_us" className="about_us list_icon" />
                                <p>收款方式</p>
                            </li>
                            <li onClick={()=>this.next_page('/change_language')}>
                                <img src={require('../../image/mine/change_language.png')} alt="about_us" className="about_us list_icon" />
                                <p>语言切换</p>
                            </li>
                            <li onClick={()=>this.next_page('/notice')}>
                                <img src={require('../../image/mine/notice.png')} alt="about_us" className="about_us list_icon" />
                                <p>平台公告</p>
                            </li>
                            <li>
                                <img src={require('../../image/mine/update.png')} alt="about_us" className="about_us list_icon" />
                                <p>检查更新</p>
                            </li>
                        </ul>
                    </div>
                    <div className="tool_bar_content quotient_content">
                        <div className="tool_bar_title">商学院</div>
                        <ul className="flex-box flex-ali-center flex-wrap tool_bar">
                            <li onClick={()=>this.next_page('/business_school?type=1')}>
                                <img src={require('../../image/mine/base.png')} alt="about_us" className="about_us list_icon" />
                                <p>基础知识</p>
                            </li>
                            <li onClick={()=>this.next_page('/business_school?type=2')}>
                                <img src={require('../../image/mine/game_rule.png')} alt="about_us" className="about_us list_icon" />
                                <p>游戏规则</p>
                            </li>
                            <li onClick={()=>this.next_page('/business_school?type=3')}>
                                <img src={require('../../image/mine/qustion.png')} alt="about_us" className="about_us list_icon" />
                                <p>常见问题</p>
                            </li>
                            <li onClick={()=>this.next_page('/opinion_fan')}>
                                <img src={require('../../image/mine/feedback.png')} alt="about_us" className="about_us list_icon" />
                                <p>意见反馈</p>
                            </li>
                        </ul>
                    </div>
                    <div className="login_out main-color-bg" onClick={this.showConfirm}>退出登录</div>
                </div>
              <Footer></Footer>
           </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Mine);
// export default Mine;


