import axios from 'axios'
    // 引入qs
import Qs from 'qs'
export const post_event = (url,value,cb) =>{
    if(!url){
        return console.warn('url is must params')
    }
    if(!value){
        return console.warn('value is must params')
    }
    if(!axios){
        return console.warn('must require axios module')
    }
    if(!Qs){
        return console.warn('must require Qs module')
    }
    if(JSON.stringify(value) != '{}'){
        value = Qs.stringify(value);
    }
    console.log(url)
    axios.post(url,value).then((ret)=>{
        if(typeof cb == 'function'){
            cb(ret.data);
        }
    }).catch((err)=>{
        if(typeof cb == 'function'){
            cb(err);
        }
    })
    
}
export const get_event = (url,value,cb) =>{
    if(!url){
        return console.warn('url is must params')
    }
    if(!value){
        return console.warn('value is must params')
    }
    if(!axios){
        return console.warn('must require axios module')
    }
    // if(JSON.stringify(value) != '{}'){
    //     value = Qs.stringify(value);
    // }
    // let get_str = '';
    // for (const key in value) {
    //     get_str += (key + '=' + value[key]) + '&'
    // }
    axios.get(url,{params:value}).then((ret)=>{
        if(typeof cb == 'function'){
            cb(ret.data);
        }
    }).catch((err)=>{
        if(typeof cb == 'function'){
            cb(err);
        }
    })
    
}


