let u = {};
u.hasCls = function (el, cls) {
    if (!u.isElement(el)) {
        console.warn('Function need el param, el param must be DOM Element');
        return;
    }
    if (el.className.indexOf(cls) > -1) {
        return true;
    } else {
        return false;
    }
}
u.isElement = function (obj) {
    return !!(obj && obj.nodeType === 1);
};


window.$api = u;


