import React, { Component } from 'react';
import './footer.css';
import { Link } from "react-router-dom";
// 导入图片
import on_home from '../../image/footer/on_home.png'
import home from '../../image/footer/home.png'
import on_transition from '../../image/footer/on_transition.png'
import transition from '../../image/footer/transition.png'
import on_wallet from '../../image/footer/on_wallet.png'
import wallet from '../../image/footer/wallet.png'
import on_mine from '../../image/footer/on_mine.png'
import mine from '../../image/footer/mine.png'

class Footer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            current_in:0
        };
    }

    change_tabbar_style(now_href){
        let current_in = 0;
        switch (now_href) {
            case '/':
                current_in = 0
                break;
        
            case '/transition':
                current_in = 1
                break;
        
            case '/index_wallet':
                current_in = 2
                break;
        
            case '/mine':
                current_in = 3
                break;
        
            default:
                break;
        }
        this.setState({
            current_in:current_in
        })
    }
    render() {
        return (
            <div>
                <div className="tabbar app_tabbar flex-box flex-ali-center flex-jus-bt main-_border-color" id="app___footer_bar">
                    <div className="footer_ele flex-box flex-jus-center flex-ali-center flex-col">
                        <Link to="/" className={this.state.current_in === 0 ? "active" : ""}>
                            <img src={this.state.current_in === 0 ? on_home : home} className="tabbar_icon" alt="商城" />
                            <p>首页</p>
                        </Link>
                    </div>
                    <div className="footer_ele flex-box flex-jus-center flex-ali-center flex-col">
                        <Link to="/transition" className={this.state.current_in === 1 ? "active" : ""}>
                            <img src={this.state.current_in === 1 ? on_transition : transition} className="tabbar_icon" alt="交易" />
                            <p>交易</p>
                        </Link>
                    </div>
                    <div className="footer_ele flex-box flex-jus-center flex-ali-center flex-col">
                        <Link to="/index_wallet" className={this.state.current_in === 2 ? "active" : ""}>
                            <img src={this.state.current_in === 2 ? on_wallet : wallet} className="tabbar_icon" alt="订单" />
                            <p>钱包</p>
                        </Link>
                    </div>
                    <div className="footer_ele flex-box flex-jus-center flex-ali-center flex-col">
                        <Link to="/mine" className={this.state.current_in === 3 ? "active" : ""}>
                            <img src={this.state.current_in === 3 ? on_mine : mine} className="tabbar_icon" alt="我的" />
                            <p>我的</p>
                        </Link>
                    </div>
                </div>
            </div>
        )
    }
    componentDidMount(){
        let path_name = this.context.router.route.location.pathname;
        this.change_tabbar_style(path_name)
    }


}

export default Footer;


