import React , { Component } from 'react';
import './register.css';
import Header from '../../components/header/header';
import $ from 'jquery'
import { message, Button, Spin, Alert } from 'antd';
import { is_phone } from "../../js/storage";
// 1. 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { post_event, get_event } from '../../js/ajax'

let inter_timer = null;
class Register extends Component {
   constructor(props){
       super(props);
       this.state = {
            is_warting:false,
            get_auth_code_str:'获取验证码',
            post_value:{}
       };
       this.back_event = this.back_event.bind(this);
       this.get_auth_code_event = this.get_auth_code_event.bind(this);
       this.input_event = this.input_event.bind(this);
       this.submit_event = this.submit_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   get_auth_code_event(){
       let _this = this;
        if(this.state.is_warting){
            return;
        }else{
            this.props.dispatch(show_screen(1));
            let post_value = this.state.post_value;
            if(!is_phone(post_value.phone)){
                this.props.dispatch(show_screen(0));
                return this.info('请输入电话号码');
            }
            let get_auth_code_params = {
                phone:post_value.phone,
                type:1,
                time:60
            }
            post_event('/api/member/getPhoneVerify',get_auth_code_params,(res)=>{
                this.props.dispatch(show_screen(0));
                if(res.success){
                    _this.info('发送成功','success');
                    let timer = 60;
                    _this.setState({
                        is_warting:true
                    })
                    inter_timer = setInterval(() => {
                        if(timer >= 1){
                            timer--;
                            _this.setState({
                                get_auth_code_str:timer + '秒后重新获取'
                            })
                        }else{
                            clearInterval(inter_timer)
                            _this.setState({
                                get_auth_code_str:'重新获取',
                                is_warting:false
                            })
                        }
                    }, 1000);
                }else{
                    _this.info(res.message)
                }
                console.log(res)
            })

            
        }
   }
   info = (msg,type) => {
        let _this = this
        message.config({
            top: '45%',
            duration: 1
        });
        if(type == 'success'){
            return message.success(msg).then(()=>{
                _this.props.dispatch(show_screen(0));
            });
        }
        message.info(msg).then(()=>{
            _this.props.dispatch(show_screen(0));
        });
    };
   submit_event(){
       let _this = this;
       let post_value = this.state.post_value;
       this.props.dispatch(show_screen(1));
       if(!post_value.phone){
           return this.info('请输入电话号码');
       }
       if(!post_value.password){
           return this.info('请输入密码');
       }
       if(!post_value.password1){
           return this.info('请输入确认密码');
       }
       if(!post_value.verify_code){
           return this.info('请输入验证码');
       }
       if(post_value.password != post_value.password1){
           return this.info('请确认密码');
       }
    //    this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('注册中...', 0)
        post_event('/api/member/register',post_value,(res)=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
            if(res.success){
                loding_alert();
                _this.info('注册成功');
                // console.log(_this.props)
                _this.props.history.push('/login')
                // _this.props.history.push('/login')
                _this.props.dispatch(show_screen(0));
            }else{
                _this.info(res.message);
            }
            console.log(res)
        })

        // setTimeout(()=>{
        //     loding_alert();
        //     _this.props.dispatch(show_screen(0));
        // }, 2500);
        console.log(this.state.post_value)
   }
   input_event(el){
       this.state.post_value[el.target.dataset.type] = el.target.value;
       this.setState({
            post_value: this.state.post_value
       })
    //    console.log(el.target.dataset.type)
    //    this.post_value
   }
   componentDidMount(){
        $('input').focus(function(){
            if(!$(this).hasClass('auth_code')){
                $(this).parents('.list_ele').css('border','1px solid #ff0078')
            }else{
                $(this).parents('.list_ele_auth_code').css('border','1px solid #ff0078')
            }
        })
        $('input').blur(function(){
            $(this).parents('.list_ele').css('border','1px solid transparent')
            $(this).parents('.list_ele_auth_code').css('border','1px solid transparent')
        })
        $('#deleted_phone_btn').click(function(){
            $(this).prev().val('')
        })
        // $('.open_close_password').click(function(){
        //     if($(this).data('open')){
        //         $(this).attr('src','../image/login/close.png');
        //         $(this).data('open',false);
        //         $(this).siblings('input').attr('type','password')
        //     }else{
        //         $(this).attr('src','../image/login/open.png');
        //         $(this).data('open',true)
        //         $(this).siblings('input').attr('type','text')
        //     }
        // })
   }
   render(){
       return (
           <div className="login_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'>注册</div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="login_page_content">
                    <div className="login_page_main">
                        <div>
                            <p className="label">手机号</p>
                            <div className="list_ele flex-box flex-ali-center bg_content">
                                <input type="text" className="phone" data-type="phone" onInput={this.input_event} placeholder="请输入您的手机号"/>
                                <img src={require('../../image/login/close.png')} id="deleted_phone_btn" alt="close" className="account" />
                            </div>
                        </div>
                        <div>
                            <p className="label">密码</p>
                            <div className="list_ele flex-box flex-ali-center bg_content">
                                <input type="password" data-type="password" onInput={this.input_event} className="password" placeholder="请输入6-18位密码"/>
                                <img src={require('../../image/login/open.png')} className="account open_close_password" alt="password" />
                            </div>
                        </div>
                        <div>
                            <p className="label">确认密码</p>
                            <div className="list_ele flex-box flex-ali-center bg_content">
                                <input type="password" data-type="password1" onInput={this.input_event} className="re_password" placeholder="请确认您的密码"/>
                                <img src={require('../../image/login/open.png')} alt="account" className="account open_close_password" />
                            </div>
                        </div>
                        <div>
                            <p className="label">邀请码(选填)</p>
                            <div className="list_ele flex-box flex-ali-center bg_content">
                                <input type="text" data-type="inviter_address" onInput={this.input_event} className="inviter_address" placeholder="请输入邀请码"/>
                                {/* <img src={require('../../image/login/open.png')} alt="account" className="account open_close_password" /> */}
                            </div>
                        </div>
                        <div>
                            <p className="label">验证码</p>
                            <div className="list_ele flex-box flex-ali-center flex-jus-bt auth_code_ele">
                                <div className="list_ele_auth_code bg_content flex-box flex-ali-center">
                                    <img src={require('../../image/login/auth_code.png')} alt="account" className="account" />
                                    <input type="text" data-type="verify_code" onInput={this.input_event} className="auth_code" placeholder="请输入验证码"/>
                                </div>
                                <div className="get_auth_code" onClick={this.get_auth_code_event}>
                                    {this.state.get_auth_code_str}
                                </div>
                            </div>
                        </div>
                        
                        <div className="login_btn main-color-bg" onClick={this.submit_event}>
                            确认
                        </div>
                    </div>
              </div>
           </div>
       )
  }
  componentWillUnmount(){
    this.setState = (state,callback)=>{
        clearInterval(inter_timer)
        console.log(state)
      return;
    };
  }
}
// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Register);
// export default Register;


