import React , { Component } from 'react';
import './transition_record.css';
import $ from 'jquery';
import Header from '../../components/header/header';
import close_icon from '../../image/transition/close_icon.png'
// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});


class Transition_record extends Component {
   constructor(props){
       super(props);
       this.state = {
            data_list:[1,2,3]
       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
     this.props.history.goBack();
   }
    //选中
   is_active(){
        $('.choose_ele_content span').each(function(index,item){
            if($(item).hasClass('choose_active')){
                $(this).append('<img src="' + close_icon + '" alt="close_icon" />')
                $(this).siblings().find('img').remove();
            }
        })
    }
   componentDidMount(){
       let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
       _this.is_active()
       // 弹出弹出框
        $('#record_icon').click(function(){
            $('#alert_content_record').css('display','block')
        })

        // 选择
        $('.alert_content_record').on('click','.choose_ele_content span',function(){
            if($(this).hasClass('choose_active')){
                $(this).removeClass('choose_active');
                $(this).find('img').remove();
            }else{
                $(this).addClass('choose_active').siblings().removeClass('choose_active')
            }
            _this.is_active()
        })

        // 重置事件
        $('#reset_btn').click(function(){
            $('.choose_ele_content span').each(function(index,item){
                if($(item).hasClass('choose_active')){
                    $(this).removeClass('choose_active')
                    $(this).find('img').remove();
                }
            })
        })
        
        // 确定事件
        $('#submit_btn').click(function(){
            $('#alert_content_record').css('display','none')
        })
   }
   next_page(id){
       console.log(id)
       let url = '/transition_record_detail?id=' + id
       this.props.history.push(url)
   }
   render(){
       return (
           <div className="transition_record_page">
              <Header>
                   <div className='header_left' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'>订单记录</div>
                   <div className='header_right'><img src={require('../../image/transition/screen.png')} className="record_icon" alt="record_icon" id="record_icon" /></div>
              </Header>
              <div id='app___page_content' className="transition_record_content">
                    <div className="transition_record_page_content">
                        <ul>
                            {
                                this.state.data_list.map((item,index)=>{
                                    return <li className="list_ele" key={index} onClick={()=>this.next_page(index)}>
                                                <div className="flex-box flex-ali-center flex-jus-bt list_ele_title">
                                                    <span className="font-size-16 text-color-pink">购买ETH</span>
                                                    <span className="font-size-14">0.076678ETH</span>
                                                    <span className="font-size-14">200CNY</span>
                                                </div>
                                                <p className="create_time">2018-7-28&nbsp;15:20</p>
                                                <p className="flex-box flex-ali-center flex-jus-bt">
                                                    <span>mmiaolian202</span>
                                                    <span className="font-size-16">已完成</span>
                                                </p>
                                            </li>
                                })
                            }
                            
                        </ul>
                    </div>
                    <div className="alert_content_record" id="alert_content_record">
                        <div className="alert_content">
                            <div>
                                <p>交易类型</p>
                                <p className="btn_group choose_ele_content">
                                    <span>购买</span>
                                    <span>出售</span>
                                </p>
                            </div>
                            <div>
                                <p>订单状态</p>
                                <p className="btn_group choose_ele_content">
                                    <span className="choose_active">未付款</span>
                                    <span>已付款</span>
                                    <span>已完成</span>
                                    <span>已取消</span>
                                    <span>申诉中</span>
                                </p>
                            </div>
                            <div>
                                <p>交易区</p>
                                <p className="btn_group choose_ele_content">
                                    <span>普通</span>
                                    <span>大宗</span>
                                </p>
                            </div>
                            <div>
                                <p className="btn_group fun_btn_group flex-box">
                                    <span className="reset_btn" id="reset_btn">重置</span>
                                    <span className="fun_btn_group_active submit_btn" id="submit_btn">确定</span>
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
           </div>
       )
  }
}


// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Transition_record);
// export default Transition_record;


