import React , { Component } from 'react';
import './add_zfb.css';

import Header from '../../components/header/header';

import { message, Button, Spin, Alert } from 'antd';
import $ from 'jquery'
// 1. 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'
class Add_zfb extends Component {
   constructor(props){
       super(props);
       this.state = {
            post_value:{}
       };
       this.back_event = this.back_event.bind(this);
       this.input_event = this.input_event.bind(this);
       this.submit_event = this.submit_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   info = (msg) => {
        let _this = this
        message.config({
            top: '45%',
            duration: 1
        });
        message.info(msg).then(()=>{
            _this.props.dispatch(show_screen(0));
        });
    };
    input_event(el){
        this.state.post_value[el.target.dataset.type] = el.target.value;
    }
    submit_event(){
        let _this = this;
        this.props.dispatch(show_screen(1));
        let post_value = this.state.post_value;
        if(!post_value.zfb_name){
            return this.info('请输入支付宝姓名',2)
        }
        if(!post_value.zfb_count){
            return this.info('请输入支付宝帐号',2);
        }
        message.config({
            top: '45%'
        });
        let loding_alert = message.loading('提交中...', 0)
        setTimeout(()=>{
            console.log(post_value)
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
    }
    componentDidMount(){
        let _this = this;
        $('#zfb_img').change(function(el){
            let file = el.target.files[0];
            // console.log(file)
            // console.log($(this))
            $(this).siblings('.camera').attr('src',URL.createObjectURL(file));
            let reader = new FileReader();
            reader.onload=function(e){
                _this.state.post_value['zfb_img'] = reader.result
                _this.setState({
                    post_value: _this.state.post_value
                })
                // console.log( reader.result);  //或者 e.target.result都是一样的，都是base64码
            }  
            reader.readAsDataURL(file)
        })
    }
   render(){
       return (
           <div className="add_zfb_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" /> 
                   </div>
                   <div className='header_title'>添加支付宝</div>
                   <div className='header_right'></div>
              </Header>
                <div id='app___page_content' className="add_zfb_content">
                    <div className="add_zfb_main">
                        <div className="list_ele">
                            <label htmlFor="zfb_name">姓名</label>
                            <input type="text" id="zfb_name" data-type="zfb_name" onInput={this.input_event} placeholder="请输入支付宝姓名"/>
                        </div>
                        <div className="list_ele">
                            <label htmlFor="zfb_count">帐号</label>
                            <input type="text" id="zfb_count" data-type="zfb_count" onInput={this.input_event} placeholder="请输入支付宝帐号"/>
                        </div>
                        <div className="list_ele">
                            <label htmlFor="zfb_img">收款二维码（选填）</label>
                            <div className="upload_img_content">
                                <input type="file" className="zfb_img" id="zfb_img" />
                                <img src={require('../../image/mine/camera.png')} alt="camera" className="camera" />
                            </div>
                        </div>
                        <div className="submit_btn main-color-bg" onClick={this.submit_event}>确定</div>
                    </div>
                </div>
           </div>
       )
  }
}
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Add_zfb);
// export default Add_zfb;


