import React , { Component } from 'react';
import './notice_detail.css';

import Header from '../../components/header/header';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});


class Notice_detail extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="notice_detail_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'>公告详情</div>
                   <div className='header_right'>
                        <img src={require('../../image/mine/share.png')} alt="back" className="back_icon" />
                   </div>
              </Header>
              <div id='app___page_content' className="notice_detail_content">
                    <div className="notice_detail_main">
                        <p className="notice_detail_main_title">2018年度积分投资分红公告</p>
                        <p className="notice_detail_main_time">2018-7-28&nbsp;&nbsp;&nbsp;阅读量&nbsp;&nbsp;&nbsp;8749</p>
                        <div className="notice_detail_main_content">
                            <p>
                                平台将于平台将于平台将于平台将于平台将于平台将于
                                平台将于平台将于平台将于平台将于平台将于平台将于
                                平台将于平台将于平台将于平台将于平台将于平台将于
                            </p>
                            <p>此处为后台自定义图文编辑区</p>
                            <p>特此公告</p>
                        </div>
                    </div>
              </div>
           </div>
       )
  }
}


// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Notice_detail);
// export default Notice_detail;


