
import { SHOWBAR,ACTIONSHEET ,SHOWSCREEN} from "./const";

export function showbar_event(){
    return {
        type:SHOWBAR,
        status:0
    }
}
export function action_sheet_show(){
    return {
        type:ACTIONSHEET,
        status:1
    }
}
export function action_sheet_hide(){
    return {
        type:ACTIONSHEET,
        status:0
    }
}
export function show_screen(status){
    return {
        type:SHOWSCREEN,
        status:status
    }
}


