// 福利之岛-购买令牌
import React , { Component } from 'react';
import './buy_token.css';
import token from '../../image/token.png'
import key_icon_pink_all from '../../image/key_icon_pink_all.png'

import $ from 'jquery'
class Buy_token extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
   }
   componentDidMount(){
        $('.buy_list_content div').click(function(){
            $(this).addClass('buy_list_content_active').siblings().removeClass('buy_list_content_active');
            $(this).find('img').attr('src',token)
            $(this).siblings().find('img').attr('src',key_icon_pink_all)
        })
   }
   render(){
       return (
           <div className="buy_token_com">
                <p className="waring_bar flex-box flex-ali-center flex-jus-bt">
                    <span className="app-color-green">持有令牌即可享受欲望之岛、冒险之岛所有钥匙收益</span>
                    <img src={require('../../image/question_blue.png')} className="choose_team_question_icon" alt="question_blue" />
                </p>
                <div className="buy_input_content flex-box flex-ali-center flex-jus-center">
                    <div className="key_content flex-box flex-ali-center flex-jus-center">
                        <img src={require('../../image/token.png')} alt="key_icon" className="key_icon"/>
                    </div>
                    <input type="text" className="buy_input_input" defaultValue="1key"/>
                    <div className="buy_input_unit flex-box flex-ali-center flex-jus-center">
                        @0.001001&nbsp;ETH
                    </div>
                </div>
                <div className="buy_list_content flex-box flex-ali-center flex-jus-center">
                    <div className="buy_list_content_active">
                        <span>+1</span>
                        <img src={require('../../image/token.png')} alt="key_icon" className="buy_list_key_icon"/>
                    </div>
                    <div><span>+50</span><img src={require('../../image/key_icon_pink_all.png')} alt="key_icon" className="buy_list_key_icon"/></div>
                    <div><span>+100</span></div>
                    <div><span>+500</span></div>
                    <div><span>+1000</span></div>
                </div>
                <div className="choose_team_content">
                    <div className="app-color-green" >提示:购买令牌其中10%贡献给社区基金</div>
                    <div className="choose_team_buy_btn">
                        购买
                    </div>
                </div>
              
           </div>
       )
  }
}

export default Buy_token;


