import React, { Component } from 'react';
import './App.css';
import { Route } from 'react-router-dom'
import {get_storage} from "./js/storage";
import { connect } from 'react-redux'
// 1. 导入actions创建函数
import { show_screen } from './store/action'


import Index from "./page/index/index";
import Transition from "./page/transition/transition";
import Transition_record from "./page/transition_record/transition_record";
import Transition_record_detail from "./page/transition_record_detail/transition_record_detail";
import Index_wallet from "./page/index_wallet/index_wallet";
import Payment from "./page/payment/payment";
import Receivables from "./page/receivables/receivables";
import Wallet_record from "./page/wallet_record/wallet_record";
import Mine from "./page/mine/mine";
import Add_payPal from "./page/add_payPal/add_payPal";
import About_us from "./page/about_us/about_us";
import Securit_center from "./page/securit_center/securit_center";
import Payment_type from "./page/payment_type/payment_type";
import Change_language from "./page/change_language/change_language";
import Notice from "./page/notice/notice";
import Notice_detail from "./page/notice_detail/notice_detail";
import Opinion_fan from "./page/opinion_fan/opinion_fan";
import Add_pay_type from "./page/add_pay_type/add_pay_type";
import Add_bank_card from "./page/add_bank_card/add_bank_card";
import Add_zfb from "./page/add_zfb/add_zfb";
import Business_school from "./page/business_school/business_school";
import Login from "./page/login/login";
import Phone_login from "./page/phone_login/phone_login";
import Register from "./page/register/register";
// import Re_cast_record from "./page/re_cast_record/re_cast_record";
// import Edit_login_password from "./page/edit_login_password/edit_login_password";
// import Edit_pay_password from "./page/edit_pay_password/edit_pay_password";
// import Market from "./page/market/market";
// import Intranet_market from "./page/intranet_market/intranet_market";

// 引入动画
import ReactCSSTransitionGroup from "react-addons-css-transition-group";
let is_new_page = false;
let update_location = false;
class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      transitionWrapper:'move_out',
      current_location:"/"

    };
    // this.listen_page_change = this.listen_page_change.bind(this);
    this.set_content_height = this.set_content_height.bind(this);
    this.set_session_storage = this.set_session_storage.bind(this);
  }
  listen_page_change(){
    console.log(this.props)
  }
  set_content_height() {
    let client_height = document.documentElement.clientHeight || document.body.clientHeight;
    let root_font_size_str = window.getComputedStyle(document.querySelector('html'),null)['font-size'];
    let root_font_size = Number(root_font_size_str.replace(/px/g,''));
    let multiple = 0;
    
    if(!window.$api.isElement(document.getElementById('app___page_content'))){return;}
    if(this.has_header()){
      multiple += 3;
      document.getElementById('app___page_content').style.paddingTop = 3 * root_font_size + 'px'
    }
    if(this.has_footer()){
      multiple += 4;
      document.getElementById('app___page_content').style.paddingBottom = 4 * root_font_size + 1 + 'px'
    }
    // let result_client_height = client_height - multiple * root_font_size;

    document.getElementById('app___page_content').style.height = client_height + 'px';
    document.getElementById('app___page_content').style.overflow = 'scroll';
    
  }

  has_header() {
    let header = document.getElementById('app___header_bar');
    return window.$api.isElement(header);
  }
  has_footer(){
    let footer = document.getElementById('app___footer_bar');
    return window.$api.isElement(footer);
  }
  set_session_storage(history_path,current_path){
    // console.log(history_path)
    let session_history_path = sessionStorage.getItem('history_path');
    session_history_path = session_history_path ? JSON.parse(session_history_path) : [];
    let animation_type = 'move_out';

    if(current_path == session_history_path[session_history_path.length -1]){
      return;
    }
    if(session_history_path.indexOf(current_path) != -1){
      animation_type = 'right';
      // animation_type = 'move_out';
      session_history_path.splice(session_history_path.indexOf(history_path),1)
    }else{
      animation_type = 'left';
      // animation_type = 'move_in';
      session_history_path.push(current_path)
    }
    if((current_path == '/' && history_path == '/consumption') || (current_path == '/' && history_path == '/market') || (current_path == '/' && history_path == '/mine')){
      animation_type = 'move_not';
    }
    if((current_path == '/consumption' && history_path == '/') || (current_path == '/consumption' && history_path == '/market') || (current_path == '/consumption' && history_path == '/mine')){
      animation_type = 'move_not';
    }
    if((current_path == '/market' && history_path == '/consumption') || (current_path == '/market' && history_path == '/') || (current_path == '/market' && history_path == '/mine')){
      animation_type = 'move_not';
    }
    if((current_path == '/mine' && history_path == '/consumption') || (current_path == '/mine' && history_path == '/') || (current_path == '/mine' && history_path == '/market')){
      animation_type = 'move_not';
    }

    animation_type = 'move_not';
    this.setState({
      transitionWrapper:animation_type
    })
    sessionStorage.setItem('history_path',JSON.stringify(session_history_path));
  }
  render() {
    return (
      
        <div className="App">
        <ReactCSSTransitionGroup
          transitionName={this.state.transitionWrapper}
          component="div"
          className={this.state.transitionWrapper}
          transitionEnterTimeout={200}
          transitionLeaveTimeout={200}>
            <Route path='/' exact component={Index}/>
            <Route path='/transition' component={Transition}/>
            <Route path='/transition_record' component={Transition_record}/>
            <Route path='/transition_record_detail' component={Transition_record_detail}/>
            <Route path='/index_wallet' component={Index_wallet}/>
            <Route path='/payment' component={Payment}/>
            <Route path='/receivables' component={Receivables}/>
            <Route path='/wallet_record' component={Wallet_record}/>
            <Route path='/mine' component={Mine}/>
            <Route path='/about_us' component={About_us}/>
            <Route path='/add_payPal' component={Add_payPal}/>
            <Route path='/securit_center' component={Securit_center}/>
            <Route path='/payment_type' component={Payment_type}/>
            <Route path='/change_language' component={Change_language}/>
            <Route path='/notice' component={Notice}/>
            <Route path='/notice_detail' component={Notice_detail}/>
            <Route path='/opinion_fan' component={Opinion_fan}/>
            <Route path='/add_pay_type' component={Add_pay_type}/>
            <Route path='/add_bank_card' component={Add_bank_card}/>
            <Route path='/add_zfb' component={Add_zfb}/>
            <Route path='/business_school' component={Business_school}/>
            <Route path='/login' component={Login}/>
            <Route path='/phone_login' component={Phone_login}/>
            <Route path='/register' component={Register}/>
            {/* <Route path='/ghk_app/about_as' component={About_as}/> */}
            {/* <Route path='/ghk_app/re_cast' component={Re_cast}/> */}
            {/* <Route path='/ghk_app/re_cast_record' component={Re_cast_record}/> */}
            {/* <Route path='/ghk_app/edit_login_password' component={Edit_login_password}/> */}
            {/* <Route path='/ghk_app/intranet_market' component={Intranet_market}/> */}
              
        </ReactCSSTransitionGroup>
        <div>
            {
              this.props.show_screen ? <div className="screen_content"></div> : null
            }

        </div>
            
        </div>
      
    );
  }
  componentWillMount(){
    if(!get_storage('user_id')){
      return this.props.history.push('/login')
    }
    this.setState({
      current_location:this.props.location.pathname
    })
  }
  componentDidMount(){
    this.set_content_height();
  }
  componentWillReceiveProps(){
    is_new_page = true;
    update_location = true;
    if(update_location){
      this.set_session_storage(this.props.location.pathname,this.props.history.location.pathname)
      this.setState({
        current_location:this.props.history.location.pathname
      })
      update_location = false;
    }
  }
  componentDidUpdate(){
    if(is_new_page){
      this.set_content_height();
        is_new_page = false;
    }
  }

}
// 3. select函数
function select(state){
  return {
    show_screen:state.show_screen
  }
}

export default connect(select)(App);



