import React , { Component } from 'react';
import './index_buy_key.css';
import $ from 'jquery'
// 导入图片
import key_icon from '../../image/key_icon.png'
import key_icon_pink_all from '../../image/key_icon_pink_all.png'
class Index_buy_key extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
   }
   componentDidMount(){

    $('.buy_list_content div').click(function(){
        $(this).addClass('buy_list_content_active').siblings().removeClass('buy_list_content_active');
        $(this).find('img').attr('src',key_icon)
        $(this).siblings().find('img').attr('src',key_icon_pink_all)
    })
   }
   render(){
       return (
           <div className="com_index_buy_key">
                <p className="waring_bar">
                    <span className="text-color-blue">现在购买</span>不少于1ETH<span className="text-color-blue">的钥匙有</span>25%<span className="text-color-blue">获得</span>100ETH<span className="text-color-blue">的空投奖励</span>
                </p>
                <div className="buy_input_content flex-box flex-ali-center flex-jus-center">
                    <div className="key_content flex-box flex-ali-center flex-jus-center">
                        <img src={require('../../image/key_icon.png')} alt="key_icon" className="key_icon"/>
                    </div>
                    <input type="text" className="buy_input_input" defaultValue="1key"/>
                    <div className="buy_input_unit flex-box flex-ali-center flex-jus-center">
                        @0.001001&nbsp;ETH
                    </div>
                </div>
                <div className="buy_list_content flex-box flex-ali-center flex-jus-center">
                    <div className="buy_list_content_active"><span>+1</span><img src={require('../../image/key_icon.png')} alt="key_icon" className="buy_list_key_icon"/></div>
                    <div><span>+50</span><img src={require('../../image/key_icon_pink_all.png')} alt="key_icon" className="buy_list_key_icon"/></div>
                    <div><span>+100</span></div>
                    <div><span>+500</span></div>
                    <div><span>+1000</span></div>
                </div>
                <div className="choose_team_content">
                    <p className="flex-box flex-ali-center flex-jus-bt">
                        <span>选择战队</span>
                        <span className="flex-box flex-ali-center team_description">战队说明<img src={require('../../image/question_blue.png')} alt="question_blue" className="choose_team_question_icon"/></span>
                    </p>
                    <ul className="team_content flex-box flex-ali-center flex-jus-bt">
                        <li className="flex-box flex-ali-center flex-jus-center flex-col">
                            <img src={require('../../image/qinglong.png')} alt="qinglong" className="team_icon"/>
                            <p>青龙</p>
                            <p>分红最多</p>
                        </li>
                        <li className="flex-box flex-ali-center flex-jus-center flex-col">
                            <img src={require('../../image/baihu.png')} alt="baihu" className="team_icon"/>
                            <p>白虎</p>
                            <p>奖池最大</p>
                        </li>
                        <li className="flex-box flex-ali-center flex-jus-center flex-col">
                            <img src={require('../../image/zhuque.png')} alt="baihu" className="team_icon"/>
                            <p>朱雀</p>
                            <p>平均分配</p>
                        </li>
                        <li className="flex-box flex-ali-center flex-jus-center flex-col">
                            <img src={require('../../image/xuanwu.png')} alt="baihu" className="team_icon"/>
                            <p>玄武</p>
                            <p>最大化ETH</p>
                        </li>
                    </ul>
                    <div className="choose_team_buy_btn">
                        购买
                    </div>
                </div>
           </div>
       )
  }
}

export default Index_buy_key;


