import React , { Component } from 'react';
import './opinion_fan.css';

import Header from '../../components/header/header';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class Opinion_fan extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="add_opinion_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'>意见反馈</div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="add_opinion_content">
                    <div className="add_opinion_main">
                        <div className="list_ele">
                            <label htmlFor="zfb_name">标题</label>
                            <input type="text" id="zfb_name" placeholder="请输入您的标题" />
                        </div>
                        <div className="list_ele">
                            <label htmlFor="opinion_content">内容</label>
                            <textarea name="opinion_content" id="opinion_content" className="opinion_content" placeholder="请输入您的详细意见和反馈"></textarea>
                        </div>
                        <div className="submit_btn main-color-bg">确定</div>
                    </div>
              </div>
           </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Opinion_fan);
// export default Opinion_fan;


