import React , { Component } from 'react';
import { BrowserRouter as Router, Route } from "react-router-dom";

// 导入页面
import App from '../App'


class routerMap extends Component {
   
   render(){
       return (
           <Router>
               <div>
                    <Route path='/' component={App}/>
               </div>
           </Router>
       )
  }
  componentWillUpdate(){}
}

export default routerMap;


