import React , { Component } from 'react';
import './payment_type.css';

import Header from '../../components/header/header';
// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { Modal, message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

const confirm = Modal.confirm;
class Payment_type extends Component {
   constructor(props){
       super(props);
       this.state = {
            pay_type_list:[1]
       };
       this.back_event = this.back_event.bind(this);
       this.deleted_type = this.deleted_type.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   next_page(url){
        this.props.history.push(url)
    }
    componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
    }
    deleted_type(name){
        let _this = this;
        confirm({
            title: '删除提醒',
            content: '您确认要删除' + name + '的收款方式吗？',
            okText:"确认",
            cancelText:"取消",
            onOk() {
                console.log('确定')
                // return 
                // new Promise((resolve, reject) => {
                // setTimeout(Math.random() > 0.5 ? resolve : reject, 1000);
                // }).catch(() => console.log('Oops errors!'));
            },
            onCancel() {},
        });
    }
   render(){
       return (
           <div className="payment_type_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                    </div>
                   <div className='header_title'>收款方式</div>
                   {
                       this.state.pay_type_list.length > 0 && <div className='header_right' onClick={()=>this.next_page('/add_pay_type')}>添加</div>
                   }
                   
              </Header>
              <div id='app___page_content' className="payment_type_content">
              {
                  this.state.pay_type_list.length > 0 && <div className="card-group">
                                                            <div className="card-block yl_gs_img">
                                                                <div className="cark-title">
                                                                    <img className="fl" src={require('../../image/mine/bankcard.png')}/>
                                                                    <span className="fl">银行卡</span>
                                                                    <span className="fr">已激活</span>
                                                                </div>
                                                                <div className="card-name">
                                                                    <span>中国工商银行</span>
                                                                </div>
                                                                <p className="card-number">22222222</p>
                                                                <div className="card-userandoprate">
                                                                    <span>张胜</span>
                                                                    <button className="fr" onClick={()=>this.deleted_type('22222222')}>删除</button>
                                                                </div>
                                                            </div>
                                                            <div className="card-block zfb_gs_bank">
                                                                <div className="cark-title">
                                                                    <img className="fl" src={require('../../image/mine/zhifubao.png')}/>
                                                                    <span className="fl">支付宝</span>
                                                                    <span className="fr">已激活</span>
                                                                </div>
                                                                <div className="card-name">
                                                                    <span>中国工商银行</span>
                                                                </div>
                                                                <p className="card-number">1111111</p>
                                                                <div className="card-userandoprate">
                                                                    <span>张胜</span>
                                                                    <button className="fr" onClick={()=>this.deleted_type('1111111')}>删除</button>
                                                                </div>
                                                            </div>
                                                        </div>
              }
              {
                  this.state.pay_type_list.length <= 0 && <div className="un_list_type flex-box flex-jus-center flex-col flex-ali-center">
                            <img src={require('../../image/mine/addpaytype.png')} alt="addpaytype" className="addpaytype"/>
                            <p className="waring_bar">您还未添加支付方式</p>
                            <div className="add_btn main-color-bg" onClick={()=>this.next_page('/add_pay_type')}>添加</div>
                        </div>
              }
                
                
              </div>
           </div>
       )
  }
}


// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Payment_type);
// export default Payment_type;


