import React , { Component } from 'react';
import './notice.css';

import Header from '../../components/header/header';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class Notice extends Component {
   constructor(props){
       super(props);
       this.state = {
            data_list:[1,1,1,1]
       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   next_page(id){
       let url = '/notice_detail?id=' + id
        this.props.history.push(url)
    }
    componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
    }
   render(){
       return (
           <div className="notice_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'>平台公告</div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="notice_page_content">
                <div className="list_content">
                {
                    this.state.data_list.map((item,index)=>{
                        return <div className="list_ele" key={index}  onClick={()=>this.next_page(index)}>
                                    <div className="list_ele_timer_bar">2018-7-28&nbsp;15:20</div>
                                    <div className="list_ele_content">
                                        <p className="list_ele_title">2018年度积分投资分红公告</p>
                                        <p>
                                            从2018年度从2018年度从2018年度从2018年度从2018年度从2018年度
                                            从2018年度从2018年度从2018年度从2018年度从2018年度从2018年度
                                            从2018年度从2018年度从2018年度从2018年度从2018年度从2018年度
                                        </p>
                                    </div>
                                    <div className="flex-box flex-ali-center flex-jus-bt list_ele_read_num">
                                        <span>阅读量&nbsp;8749</span>
                                        <span>分享</span>
                                    </div>
                                </div>
                    })
                }
                    
	            </div>
              </div>
           </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Notice);
// export default Notice;


