import React , { Component } from 'react';
import './index_wallet.css';

import Header from '../../components/header/header';
import Footer from '../../components/footer/footer';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class Index_wallet extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
   }
   next_page(path){
        this.props.history.push(path);
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="index_wallet_page">
              <Header>
                   <div className='header_left'></div>
                   <div className='header_title'>我的钱包</div>
                   <div className='header_right header_rgiht_content' onClick={()=>this.next_page('/wallet_record')}>明细</div>
              </Header>
              <div id='app___page_content' className="index_wallet_content">
                    <div className="index_wallet_main">
                        <div className="top-num">
                            <div className="">
                                <span>2894090394502.99 </span>
                                <img src={require('../../image/wallet/eth_wallet.png')} alt="eth_wallet" className="icon-eth"/>
                            </div>
                            <p className="">≈78378442CNY</p>
                        </div>
                        <div className="flex-box flex-ali-center flex-jus-ad nav-icon">
                            <div onClick={()=>this.next_page('/payment')}>
                                <img src={require('../../image/wallet/pay.png')} alt="pay"/>
                                <div>付款</div>
                            </div>
                            <div onClick={()=>this.next_page('/receivables')}>
                                <img src={require('../../image/wallet/get.png')} alt="get"/>
                                <div>收款</div>
                            </div>
                            <div onClick={()=>this.next_page('/transition')}>
                                <img src={require('../../image/wallet/trans.png')} alt="trans"/>
                                <div>交易</div>
                            </div>
                        </div>
                        <div className="trans-wrap near_future">
                            <div className="title">近期交易</div>
                            <div className="sub-title flex-box flex-ali-center flex-jus-bt">
                                <span className="txt-red">转入ETH</span>
                                <span>已完成</span>
                            </div>
                            <div className="table-wrap">
                                <div className="table-row">
                                    <div className="table-td">金额</div>
                                    <div className="table-td">对方地址</div>
                                    <div className="table-td">2018-07-04 14:00</div>
                                </div>
                                <div className="table-body">
                                    <div className="table-td txt-red">+9555.7</div>
                                    <div className="table-td">https://bakdad.adg.com/dadgadgadgadgad</div>
                                </div>
                            </div>
                        </div>
                        <div className="trans-wrap">
                            <div className="sub-title flex-box flex-ali-center flex-jus-bt">
                                <span className="txt-blue">转出ETH</span>
                                <span>已完成</span>
                            </div>
                            <div className="table-wrap">
                                <div className="table-row">
                                    <div className="table-td">金额</div>
                                    <div className="table-td">对方地址</div>
                                    <div className="table-td">2018-07-04 14:00</div>
                                </div>
                                <div className="table-body">
                                    <div className="table-td txt-blue">-9555.7</div>
                                    <div className="table-td">https://bakdad.adg.com/dadgadgadgadgad</div>
                                </div>
                            </div>
                        </div>
                    </div>
              </div>

              <Footer></Footer>
           </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Index_wallet);

// export default Index_wallet;


