import React , { Component } from 'react';
import './login.css';

import $ from 'jquery'
// import Header from '../../components/header/header';
import {set_storage,set_remember_storage,get_remember_storage,deleted_storage} from '../../js/storage'
import { message, Button, Spin, Alert } from 'antd';

// 1. 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { post_event } from '../../js/ajax'

class Login extends Component {
   constructor(props){
       super(props);
       this.state = {
            is_login:true,
            user_msg:{}
       };
       this.login_event = this.login_event.bind(this)
   }
   componentDidMount(){
        $('input').focus(function(){
            $(this).parents('.list_ele').css('border','1px solid #ff0078');
        })
        $('input').blur(function(){
            $(this).parents('.list_ele').css('border','1px solid transparent')
            $(this).parents('.list_ele_auth_code').css('border','1px solid transparent')
        })
        // 判断是否记住密码
        let user_msg = get_remember_storage();
        this.setState({
            user_msg:user_msg
        })

        // $('.open_close_password').click(function(){
        //     if($(this).data('open')){
        //         $(this).attr('src','../image/login/close.png');
        //         $(this).data('open',false);
        //         $(this).siblings('input').attr('type','password')
        //     }else{
        //         $(this).attr('src','../image/login/open.png');
        //         $(this).data('open',true)
        //         $(this).siblings('input').attr('type','text')
        //     }
        // })
   }
   info = (msg) => {
        let _this = this
        message.config({
            top: '45%',
            duration: 2
        });
        message.info(msg).then(()=>{
            _this.props.dispatch(show_screen(0));
        });
    };
   next_page(url){
       this.props.history.push(url)
   }
   login_event(){
       let _this = this;
       this.props.dispatch(show_screen(1));
       let post_value = {
            phone:$('#account').val(),
            password:$('#password').val(),
       }
       if(!post_value.phone){
            return this.info('请输入帐号',2)
       }
       if(!post_value.password){
            return this.info('请输入密码',2);
       }
        message.config({
            top: '45%'
        });
        let loding_alert = message.loading('登录中...', 0)
        post_event('/api/member/login',post_value,(res)=>{
            loding_alert();
            if(res.success){
                 // 记住密码
                let is_checked = $('#is_remember').is(':checked');
                if(is_checked){
                    set_remember_storage({phone: post_value.phone, password: post_value.password})
                }else{
                    deleted_storage('fomo3dd_remember')
                }
                set_storage('user_id',res.data.token);
                 _this.props.dispatch(show_screen(0));
                _this.props.history.push('/');
            }else{
                _this.info(res.message);
            }
        })
       
       
       

   }
   render(){
       return (
           <div className="login_page">
              {/* <Header>
                   <div className='header_left back_icon'></div>
                   <div className='header_title'></div>
                   <div className='header_right'>注册</div>
              </Header> */}
              <div id='app___page_content' className="login_page_content">
                    <div className="login_page_main">
                        <div className="register_btn"><span onClick={()=>this.next_page('/register')}>注册</span></div>
                        <div className="flex-box flex-ali-center flex-jus-center">
                            <img src={require('../../image/logo.png')} alt="logo" className="logo" />
                        </div>
                        <div className="list_ele flex-box flex-ali-center">
                            <img src={require('../../image/login/phone.png')} alt="account" className="account" />
                            <input type="text" placeholder="请输入您的帐号" id="account" defaultValue={this.state.user_msg.phone}/>
                        </div>
                        <div className="list_ele flex-box flex-ali-center">
                            <img src={require('../../image/login/password.png')} alt="account" className="account" />
                            <input type="password" placeholder="请输入您的密码" id="password" defaultValue={this.state.user_msg.password}/>
                        </div>
                        <div className="flex-box flex-ali-center flex-jus-bt forget_password_bar">
                            <div className="flex-box flex-ali-center">
                                <input className="checked_box"  type="checkbox" id="is_remember" />
                                <label htmlFor="is_remember" className="remember_password">记住密码</label>
                            </div>
                            <div className="forget_password">忘记密码</div>
                        </div>
                        <div className="login_btn main-color-bg" onClick={this.login_event}>
                            登陆
                        </div>
                        <div className="phone_login" onClick={()=>this.next_page('/phone_login')}>手机验证码登录</div>
                    </div>
              </div>
              
           </div>
       )
  }
}
// 3. select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Login);


