import React , { Component } from 'react';
import './business_school.css';
import $ from 'jquery'
import Header from '../../components/header/header';
import group1 from '../../image/mine/group1.png'
import group2 from '../../image/mine/group2.png'

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});


class Business_school extends Component {
   constructor(props){
       super(props);
       this.state = {
            current_index:1,
            data_list:[
                {img:group1},
                {img:group2},
                {img:group1},
                {img:group2},
                {img:group1},
                {img:group2},
                {img:group1},
                {img:group2},
            ]
       };
       this.back_event = this.back_event.bind(this);
       this.choose_tab = this.choose_tab.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentWillMount(){
        let current_index = this.props.location.search ? this.props.location.search.split('=')[1] : 1;
        this.setState({
            current_index:Number(current_index)
        })
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
    choose_tab(index){
        this.setState({
            current_index:index
        })
    }
   render(){
       return (
           <div className="business_school_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" /> 
                   </div>
                   <div className='header_title'>商学院</div>
                        <img src={require('../../image/mine/search.png')} alt="back" className="back_icon" /> 
                   <div className='header_right'>
                   </div>
              </Header>
              <div id='app___page_content' className="business_school_content">
              <div className="wallet_record_main">
                    <div className="business_school_page_content">
                        <div className="business_school_page_content_header_content">
                            <div className="business_school_page_content_header">
                                <div onClick={()=>this.choose_tab(1)}><span className={[this.state.current_index === 1 && 'business_school_page_content_header_active'].join(' ')}>基础知识</span></div>
                                <div onClick={()=>this.choose_tab(2)}><span className={[this.state.current_index === 2 && 'business_school_page_content_header_active'].join(' ')}>游戏规则</span></div>
                                <div onClick={()=>this.choose_tab(3)}><span className={[this.state.current_index === 3 && 'business_school_page_content_header_active'].join(' ')}>常见问题</span></div>
                            </div>
                        </div>
                        {/* <!-- 主页面 --> */}
                        <ul className="business_school_page_list_content">
                        {
                            this.state.data_list.map((item,index)=>{
                                return <li className="list_ele flex-box flex-ali-center flex-jus-bt" key={index}>
                                    <div className="list_ele_content">从概念到底层技术从概念到底层技术从概念到底层技术从概念到底层技术</div>
                                    <div className="img_content">
                                        <img src={item.img} alt="img" className="list_ele_img"/>
                                    </div>
                                </li>
                            })
                        }
                            
                        </ul>
                    </div>
                        
                </div>
              </div>
           </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Business_school);
// export default Business_school;


