import React , { Component } from 'react';
import './phone_login.css';

import $ from 'jquery'
import { message, Button, Spin, Alert } from 'antd';

import { is_phone } from "../../js/storage";
// 1. 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { post_event, get_event } from '../../js/ajax'
import {set_storage} from '../../js/storage'
let inter_timer = null;
class Phone_login extends Component {
   constructor(props){
       super(props);
       this.state = {
            is_warting:false,
            get_auth_code_str:'获取验证码',
            post_value:{}
       };
       this.back_event = this.back_event.bind(this);
       this.get_auth_code_event = this.get_auth_code_event.bind(this);
       this.submit_event = this.submit_event.bind(this);
       this.input_event = this.input_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   get_auth_code_event(){
       let _this = this;
        if(this.state.is_warting){
            return;
        }else{
            this.props.dispatch(show_screen(1));
            let post_value = this.state.post_value;
            console.log(is_phone(post_value.phone))
            if(!is_phone(post_value.phone)){
                return this.info('请输入正确的电话号码');
            }
            let get_auth_code_params = {
                phone:post_value.phone,
                type:3,
                time:60
            }
            post_event('/api/member/getPhoneVerify',get_auth_code_params,(res)=>{
                this.props.dispatch(show_screen(0));
                if(res.success){
                    _this.info('发送成功','success');
                    let timer = 60;
                    _this.setState({
                        is_warting:true
                    })
                    inter_timer = setInterval(() => {
                        if(timer >= 1){
                            timer--;
                            _this.setState({
                                get_auth_code_str:timer + '秒后重新获取'
                            })
                        }else{
                            clearInterval(inter_timer)
                            _this.setState({
                                get_auth_code_str:'重新获取',
                                is_warting:false
                            })
                        }
                    }, 1000);
                }else{
                    _this.info(res.message)
                }
                console.log(res)
            })


            // this.info('发送成功','success');
            // let timer = 60;
            // this.setState({
            //     is_warting:true
            // })
            // inter_timer = setInterval(() => {
            //     if(timer >= 1){
            //         timer--;
            //         this.setState({
            //             get_auth_code_str:timer + '秒后重新获取'
            //         })
            //     }else{
            //         clearInterval(inter_timer)
            //         this.setState({
            //             get_auth_code_str:'重新获取',
            //             is_warting:false
            //         })
            //     }
            // }, 1000);
        }
    }
    input_event(el){
        this.state.post_value[el.target.dataset.type] = el.target.value;
        this.setState({
            post_value:this.state.post_value
        })
    }
    next_page(url){
        this.props.history.push(url)
    }
    info = (msg,type) => {
        let _this = this
        message.config({
            top: '45%',
            duration: 2
        });
        if(type == 'success'){
            return message.success(msg).then(()=>{
                _this.props.dispatch(show_screen(0));
            });
        }
        message.info(msg).then(()=>{
            _this.props.dispatch(show_screen(0));
        });
    };
    submit_event(){
        let _this = this;
        this.props.dispatch(show_screen(1));
        let post_value = this.state.post_value;
        if(!is_phone(post_value.phone)){
            return this.info('请输入正确的电话号码');
        }
        if(!post_value.verify_code){
            return this.info('请输入验证码');
        }
        post_value.type = 3
        let loding_alert = message.loading('登录中...', 0);
        post_event('/api/member/smsLogin',post_value,(res)=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
            if(res.success){
                loding_alert();
                set_storage('user_id',res.data.token);
                _this.props.dispatch(show_screen(0));
                _this.props.history.push('/')
            }else{
                _this.info(res.message);
            }
            console.log(res)
        })
        // setTimeout(()=>{
        //     loding_alert();
        //     _this.props.dispatch(show_screen(0));
        // }, 2500);
        console.log(this.state.post_value)

    }
    componentDidMount(){
        $('input').focus(function(){
            if(!$(this).hasClass('auth_code')){
                $(this).parents('.list_ele').css('border','1px solid #ff0078')
            }else{
                $(this).parents('.list_ele_auth_code').css('border','1px solid #ff0078')
            }
        })
    $('input').blur(function(){
        $(this).parents('.list_ele').css('border','1px solid transparent')
        $(this).parents('.list_ele_auth_code').css('border','1px solid transparent')
    })
    // $('.open_close_password').click(function(){
    //     if($(this).data('open')){
    //         $(this).attr('src','../image/login/close.png');
    //         $(this).data('open',false);
    //         $(this).siblings('input').attr('type','password')
    //     }else{
    //         $(this).attr('src','../image/login/open.png');
    //         $(this).data('open',true)
    //         $(this).siblings('input').attr('type','text')
    //     }
    // })
   }
   render(){
       return (
           <div className="phone_login_page">
              <div id='app___page_content' className="phone_login_page_content">
                <div className="phone_login_page_main">
                        <div className="register_btn"><span onClick={()=>this.next_page('/register')}>注册</span></div>
                        <div className="flex-box flex-ali-center flex-jus-center">
                            <img src={require('../../image/logo.png')} alt="logo" className="logo" />
                        </div>
                        <div className="list_ele flex-box flex-ali-center bg_content">
                            <img src={require('../../image/login/phone.png')} alt="account" className="account" />
                            <input type="text" className="phone" data-type="phone" onInput={this.input_event} placeholder="请输入您的手机号"/>
                        </div>
                        <div className="list_ele flex-box flex-ali-center flex-jus-bt auth_code_ele">
                            <div className="list_ele_auth_code bg_content flex-box flex-ali-center">
                                <img src={require('../../image/login/auth_code.png')} alt="account" className="account" />
                                <input type="text" data-type="verify_code" onInput={this.input_event} className="auth_code" placeholder="请输入验证码"/>
                            </div>
                            <div className="get_auth_code" onClick={this.get_auth_code_event}>
                                {this.state.get_auth_code_str}
                            </div>
                        </div>
                        
                        <div className="phone_login_btn main-color-bg" onClick={this.submit_event}>
                            登陆
                        </div>
                        <div className="phone_login" onClick={this.back_event}>帐号密码登录</div>
                    </div>
                </div>
           </div>
       )
  }
  componentWillUnmount(){
    this.setState = (state,callback)=>{
        clearInterval(inter_timer)
        console.log(state)
      return;
    };
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Phone_login);
// export default Phone_login;


