import React , { Component } from 'react';
import './about_us.css';

import Header from '../../components/header/header';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class About_us extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="about_us">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'>关于我们</div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="about_us_content">
                    <div className="img_content">
                        <img className="aboutus-img" src={require('../../image/mine/headplayer.png')} alt="headplayer"/>
                    </div>
                    <div className="contain-block">
                        ad刷卡缴费卡老师您垫付款揽胜的快乐飞耐饿欧诺法流口水呢疯狂拉升你的福利卡师傅
                        ad刷卡缴费卡老师您垫付款揽胜的快乐飞耐饿欧诺法流口水呢疯狂拉升你的福利卡师傅
                        ad刷卡缴费卡老师您垫付款揽胜的快乐飞耐饿欧诺法流口水呢疯狂拉升你的福利卡师傅
                    </div>
              </div>
           </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(About_us);
