// 欲望之岛
import React , { Component } from 'react';
import './index_desire_islands.css';
import $ from "jquery";

// 导入组件
// 购买钥匙
import IndexBuyKey from "../index_buy_key/index_buy_key";
// 我的金库
import MineVault from "../mine_vault/mine_vault";
// 邀请玩家
import InvitePlayer from "../invite_player/invite_player";

// 本轮游戏
import CurrentGame from "../current_game/current_game";
// 战队统计
import TeamCount from "../team_count/team_count";
// 最新玩家
import NewPlayer from "../new_player/new_player";
// 投资统计
import InvestCount from "../invest_count/invest_count";

class Index_desire_islands extends Component {
   constructor(props){
       super(props);
       this.state = {
            first_url:'buy_key',
            second_url:'current_game'
       };
   }
   componentDidMount(){
       let _this = this;
        $('.group_content_1_btn_group div').click(function(){
            $(this).addClass('group_content_1_btn_group_active').siblings().removeClass('group_content_1_btn_group_active')
            var url = this.dataset.href;
            _this.setState({
                first_url:url
            })
            console.log(url)
            // change_model(default_top_url,default_top_content_id);
        })
        $('.group_content_2_btn_group div').click(function(){
            $(this).addClass('group_content_2_btn_group_active').siblings().removeClass('group_content_2_btn_group_active');
            var url1 = this.dataset.href;
            _this.setState({
                second_url:url1
            })
            // default_under_url = this.dataset.href;
            // console.log(default_under_url)
            // change_model(default_under_url,default_under_content_id)
        })
   }
   render(){
       return (
           <div className="islands_component">
              <div className="group_content_1">
                    <div className="four_corner">
                        <div className="left_top"></div>
                        <div className="right_top"></div>
                        <div className="left_bottom"></div>
                        <div className="right_bottom"></div>
                    </div>
                    <div className="group_content_1_btn_group flex-box flex-ali-center flex-jus-sa">
                        <div className="group_content_1_btn_group_active" data-href="buy_key">购买钥匙</div>
                        <div data-href="mine_vault">我的金库</div>
                        <div data-href="invite_player">邀请玩家</div>
                    </div>
                    <div id="group_content_1_content">
                    {
                        this.state.first_url == 'buy_key' && <IndexBuyKey></IndexBuyKey> 
                    }
                    {
                        this.state.first_url == 'mine_vault' && <MineVault></MineVault> 
                    }
                    {
                        this.state.first_url == 'invite_player' && <InvitePlayer></InvitePlayer> 
                    }
                    </div>
                </div>
                {/* <!-- 游戏 --> */}
                <div className="group_content_2">
                    <div className="four_corner">
                        <div className="left_top"></div>
                        <div className="right_top"></div>
                        <div className="left_bottom"></div>
                        <div className="right_bottom"></div>
                    </div>
                    <div className="group_content_2_btn_group flex-box flex-ali-center flex-jus-sa">
                        <div className="group_content_2_btn_group_active" data-href="current_game">本轮游戏</div>
                        <div data-href="team_count">战队统计</div>
                        <div data-href="new_player">最新玩家</div>
                        <div data-href="invest_count">投资统计</div>
                    </div>
                    <div id="group_content_2_content">
                    {
                        this.state.second_url == 'current_game' && <CurrentGame></CurrentGame> 
                    }
                    {
                        this.state.second_url == 'team_count' && <TeamCount></TeamCount> 
                    }
                    {
                        this.state.second_url == 'new_player' && <NewPlayer></NewPlayer> 
                    }
                    {
                        this.state.second_url == 'invest_count' && <InvestCount></InvestCount> 
                    }
                    </div>
                </div> 
           </div>
       )
  }
}

export default Index_desire_islands;


