import React , { Component } from 'react';
import './payment.css';

import Header from '../../components/header/header';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});

class Payment extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
   }
   render(){
       return (
           <div className="payment_page">
                <Header>
                    <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                    </div>
                    <div className='header_title'>付款</div>
                    <div className='header_right'></div>
                </Header>
                <div id='app___page_content' className="payment_page_content">
                    <div className="payment_main">
                        <div>
                            <p className="text-size-16">发送方</p>
                            <p className="text-size-14">https://www.baidu.com</p>
                        </div>
                        <div>
                            <p className="text-size-16">发送给</p>
                            <div className="input_content"><input type="text" placeholder="请输入接收方地址" /></div>
                        </div>
                        <div>
                            <p className="text-size-16">数量</p>
                            <div className="flex-box flex-ali-center flex-jus-bt input_content">
                                <input type="text" placeholder="请输入支付的数量"/><span className="all_btn">全部</span>
                            </div>
                        </div>
                        <p className="money_bar">可用ETH：<span className="text-size-16 text-color-white">1789.67</span></p>
                        <div className="text-size-16 submit_btn main-color-bg">支付</div>
                        <p className="waring_bar"><span className="text-color-red margin-right-4">*</span>由于经常出现堵塞或超时，请在交易记录确认，以免重复操作</p>
                    </div>
                </div>
           </div>
       )
  }
}

// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Payment);
// export default Payment;


