// 常量
import { SHOWBAR, ACTIONSHEET, SHOWSCREEN } from "./const";

// 导入合并函数
import { combineReducers } from "redux";

function change_status(state = 0, action){
    switch (action.type) {
        case SHOWBAR:
            
            return action.status;
    
        default:
            return state;
    }
}

function action_sheet_status(state = 0, action){
    switch (action.type) {
        case ACTIONSHEET:
            return action.status;
    
        default:
            return state;
    }
}

function show_screen(state = false, action){
    switch (action.type) {
        case SHOWSCREEN:
            return action.status;
    
        default:
            return state;
    }
}

export default combineReducers({change_status,action_sheet_status,show_screen})


