import React , { Component } from 'react';
import './add_payPal.css';

import Header from '../../components/header/header';
import { message, Button, Spin, Alert } from 'antd';

// 1. 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'


class Add_payPal extends Component {
   constructor(props){
       super(props);
       this.state = {
            post_value:{}
       };
       this.back_event = this.back_event.bind(this);
       this.input_event = this.input_event.bind(this);
       this.submit_event = this.submit_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   info = (msg) => {
        let _this = this
        message.config({
            top: '45%',
            duration: 1
        });
        message.info(msg).then(()=>{
            _this.props.dispatch(show_screen(0));
        });
    };
    input_event(el){
        this.state.post_value[el.target.dataset.type] = el.target.value;
    }
    submit_event(){
        let _this = this;
        this.props.dispatch(show_screen(1));
        let post_value = this.state.post_value;
        if(!post_value.zfb_name){
            return this.info('请输入支付宝姓名',2)
        }
        if(!post_value.payPal_count){
            return this.info('请输入payPal帐号',2);
        }
        message.config({
            top: '45%'
        });
        let loding_alert = message.loading('提交中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
    }
   render(){
       return (
           <div className="add_payPal_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" /> 
                   </div>
                   <div className='header_title'>添加PayPal</div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="add_payPal_content">
                    {/* <div class="add_payPal_page"> */}
                        <div className="list_ele">
                            <label htmlFor="zfb_name">姓名</label>
                            <input type="text" onInput={this.input_event} data-type="zfb_name" id="zfb_name" placeholder="请输入支付宝姓名"/>
                        </div>
                        <div className="list_ele">
                            <label htmlFor="payPal_count">帐号</label>
                            <input type="text" id="payPal_count" onInput={this.input_event} data-type="payPal_count" onInput={this.input_event} placeholder="请输入payPal帐号"/>
                        </div>
                        <div className="submit_btn main-color-bg" onClick={this.submit_event}>确定</div>
                    {/* </div> */}
              </div>
           </div>
       )
  }
}
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Add_payPal);
// export default Add_payPal;


