export const get_storage = (key) =>{
    var res_str = localStorage.getItem('fomo3dd');
    // console.log(res_str)
    if(res_str){
        let res_json = JSON.parse(res_str);
        return res_json[key];
    }else{
        return null;
    }
}

export const get_remember_storage = () =>{
    var res_str = localStorage.getItem('fomo3dd_remember');
    // console.log(res_str)
    if(res_str){
        let res_json = JSON.parse(res_str);
        return res_json;
    }else{
        return {};
    }
}

export const set_storage = (key,value) =>{
    var res_str = localStorage.getItem('fomo3dd');
    var set_obj = {};
    if(res_str){
        set_obj = JSON.parse(res_str);
        set_obj[key] = value
    }else{
        set_obj[key] = value
    }
    let set_result_str = JSON.stringify(set_obj)
    localStorage.setItem('fomo3dd',set_result_str)
}

export const set_remember_storage = (value) =>{
    // var res_str = localStorage.getItem('fomo3dd_remember');
    // var set_obj = {};
    // if(res_str){
    //     set_obj = JSON.parse(res_str);
    //     set_obj[key] = value
    // }else{
    //     set_obj[key] = value
    // }
    let set_result_str = JSON.stringify(value)
    localStorage.setItem('fomo3dd_remember',set_result_str)
}

export const clear_storage = () =>{
    localStorage.clear();
}

export const deleted_storage = (key) =>{
    localStorage.removeItem(key)
}
export const is_phone = (phone) =>{
    if (!/(13\d|14[57]|15[^4,\D]|17[13678]|18\d)\d{8}|170[0589]\d{7}/.test(phone) ){
        return false;
    }else{
        return true
    }
}

