import React , { Component } from 'react';
import './wallet_record.css';
import $ from 'jquery'
import Header from '../../components/header/header';

// 导入actions创建函数
import { connect } from 'react-redux'
import { show_screen } from '../../store/action'

import { message, Button, Spin, Alert } from 'antd';
message.config({
    top: '45%'
});


class Wallet_record extends Component {
   constructor(props){
       super(props);
       this.state = {

       };
       this.back_event = this.back_event.bind(this);
   }
   back_event(){
        this.props.history.goBack();
   }
   componentDidMount(){
        let _this = this;
        // 加载中
        this.props.dispatch(show_screen(1));
        let loding_alert = message.loading('加载中...', 0)
        setTimeout(()=>{
            loding_alert();
            _this.props.dispatch(show_screen(0));
        }, 2500);
        $('.wallet_record_page_content_header div').click(function(){
            $(this).find('span').addClass('wallet_record_page_content_header_active');
            $(this).siblings().find('span').removeClass('wallet_record_page_content_header_active')
        })
   }
   render(){
       return (
           <div className="wallet_record_page">
              <Header>
                   <div className='header_left back_icon' onClick={this.back_event}>
                        <img src={require('../../image/back.png')} alt="back" className="back_icon" />
                   </div>
                   <div className='header_title'>钱包明细</div>
                   <div className='header_right'></div>
              </Header>
              <div id='app___page_content' className="wallet_record_content">
                    <div className="wallet_record_main">
                        <div className="wallet_record_page_content">
                            <div className="wallet_record_page_content_header_content">
                                <div className="wallet_record_page_content_header">
                                    <div><span className="wallet_record_page_content_header_active">转账记录</span></div>
                                    <div><span>收款记录</span></div>
                                    <div><span>付款记录</span></div>
                                </div>
                            </div>
                            {/* <!-- 主页面 --> */}
                            <ul className="wallet_record_page_list_content">
                                <li className="list_ele">
                                    <div className="list_ele_title flex-box flex-ali-center flex-jus-bt">
                                        <span className="font-size-20 text-color-pink">转入ETH</span>
                                        <span className="font-size-20 text-color-white">已完成</span>
                                    </div>
                                    <div className="list_ele_label_bar flex-box flex-ali-center flex-jus-bt">
                                        <div className="list_ele_price_label font-size-14">金额</div>
                                        <div className="list_ele_address_label flex-box flex-ali-center flex-jus-bt">
                                            <span className="font-size-14">对方地址</span>
                                            <span className="font-size-14">2018-7-28&nbsp;15:20</span>
                                        </div>
                                    </div>
                                    <div className="list_ele_price_bar flex-box flex-ali-center flex-jus-bt">
                                        <div className="list_ele_price_label font-size-20 text-color-pink">+9.7</div>
                                        <div className="list_ele_address_label">
                                            <span className="font-size-14">http://www.baidu.comhttp://www.baidu.com</span>
                                        </div>
                                    </div>
                                </li>
                                <li className="list_ele">
                                    <div className="list_ele_title flex-box flex-ali-center flex-jus-bt">
                                        <span className="font-size-20 text-color-blue">转出ETH</span>
                                        <span className="font-size-20 text-color-white">已完成</span>
                                    </div>
                                    <div className="list_ele_label_bar flex-box flex-ali-center flex-jus-bt">
                                        <div className="list_ele_price_label font-size-14">金额</div>
                                        <div className="list_ele_address_label flex-box flex-ali-center flex-jus-bt">
                                            <span className="font-size-14">对方地址</span>
                                            <span className="font-size-14">2018-7-28&nbsp;15:20</span>
                                        </div>
                                    </div>
                                    <div className="list_ele_price_bar flex-box flex-ali-center flex-jus-bt">
                                        <div className="list_ele_price_label font-size-20 text-color-blue">+9.7</div>
                                        <div className="list_ele_address_label">
                                            <span className="font-size-14">http://www.baidu.comhttp://www.baidu.com</span>
                                        </div>
                                    </div>
                                </li>
                            </ul>
                        </div>
                        
                    </div>
              </div>
           </div>
       )
  }
}


// select函数
function select(state){
    return {
        show_screen:state.show_screen
    }
}

export default connect(select)(Wallet_record);
// export default Wallet_record;


